.benefits_list {
  font-family: $rubik;
  display: flex;
  justify-content: space-around;

  padding: 30px 0 30px 0;

}

.header_box {
  font-family: $rubik;
  line-height: 100px;

  display: flex;
  justify-content: space-between;
  &_logo {
  display: flex;
    align-items: center;
    justify-content: center;

    img {
      height: 100px;
      width: 100px;
      margin-right: 20px;
    }
    h1 {
      font-size: 50px;
      font-weight: $font-extrabold;
      text-transform: uppercase;
    }
  }
  &_menu_list {
    display: flex;
    justify-content: space-around;
    align-items: center;

    font-size: 20px;
    &_element {
      margin-left: 20px;
    }
  }
}


